@import "./../base/_grid.scss";
.usersTable {
    width: 100%;
    display: flex;
    flex-direction: column;

    justify-content: flex-start;
}
.tableUsers{
    width: 100%;
    margin-top: 2.4rem;
    .headerRow{
        padding: 1.5rem 2rem;
        display: flex;
        flex-direction: row;
        background-color: #f5f5f5;
        border-radius: 8px 8px 0 0;
        .headerItem{
            flex: 1;
        }
    }
    .contentRow{
        padding: 1rem 2rem;
        display: flex;
        flex-direction: row;
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        .contentItem{
            flex: 1;
            a:link{
                text-decoration: none;
                color: black;
            }
            a:visited{
                color: black;
            }
        }  
    }
}

.tablePosicions{
    width: 80%;
    margin-top: 2rem;
    .headerRow{
        padding: 1.5rem 2rem;
        display: flex;
        flex-direction: row;
        background-color: #f5f5f5;
        border-radius: 8px 8px 0 0;
        .headerItem{
            flex: 1;
        }
    }
    .contentRow{
        padding: 1rem 2rem;
        display: flex;
        flex-direction: row;
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        .contentItem{
            flex: 1;
        }  
        
    }
}
.containerButtons {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
        }
.flex-end{
    display: flex;
    justify-content: center;
    align-items: center;
}